.navbar {
    background-color: #3f51b5;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .logo {
    color: white;
    margin-left: 2rem;
  }
  .menu-bars {
    margin-right: 2rem;
    font-size: 2rem;
    background: none;
    background-color: #3f51b5;
  }
  
  .nav-menu {
    background-color: #3f51b5;
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
  }
  
  .nav-menu.active {
    left: 0;
    transition: 350ms;
  }
  
  .nav-text {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
  }
  
  .nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
  }
  
  .nav-text a:hover {
    background-color: white;
    color: #060b26;
  }
  
  .nav-menu-items {
    width: 100%;
  }
  
  .navbar-toggle {
    background-color: #3f51b5;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }
  
  span {
    margin-left: 16px;
  }
  
  .header-btns button {
    margin-right: 2rem;
    padding: 0.7rem;
    background: #3f51b5;
    color: white;
    border: 1.5px solid white;
    border-radius: 5px;
    cursor: pointer;
    font-weight: bold;
  }
  .header-btns button:hover {
    background: white;
    color: #3f51b5;
  }
  .side-btns {
    display: flex;
    flex-direction: column;
  }
  
  .side-btns button {
    margin: 1rem;
    background: #3f51b5;
    color: white;
    border: 1.5px solid white;
    border-radius: 5px;
    cursor: pointer;
    font-weight: bold;
    padding: 1rem;
  }
  .side-btns button:hover {
    background: white;
    color: #060b26;
  }
  
  @media (min-width: 992px) {
    .menu-bars {
      display: none;
    }
    .nav-menu {
      display: none;
    }
  }
  @media (max-width: 992px) {
    .header-btns {
      display: none;
    }
  }
  @media (max-width: 375px) {
    .navbar {
      width: 100vw;
    }
  }
  