@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Lato", sans-serif;
}

.home {
  display: -webkit-flex;
  display: flex;
  font-size: 3rem;
}
.dashboard-cards {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.dashboard-card {
  box-shadow: 4px 4px 10px #3f51b5;
  margin: 2rem;
  width: 20rem;
  height: 10rem;
  padding: 1rem;
  background-color: white;
  border-radius: 1rem;
}

.card-head {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  font-size: 1.1rem;
}
.treasury-icon {
  background: red;
  color: white;
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
  padding: 4px;
}
.rewards-icon {
  background: green;
  color: white;
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
  padding: 4px;
}
.thumb-icon {
  background: blue;
  color: white;
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
  padding: 4px;
  margin-left: 0.139rem;
}
.clock-icon {
  background: orange;
  color: white;
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
  padding: 4px;
}
.card-amount {
  font-size: 1.5rem;
  margin-top: 1rem;
}
.cobalt-card {
  box-shadow: 4px 4px 10px #3f51b5;
  margin: 2rem;
  width: 20rem;
  height: 12rem;
  padding: 1rem;
  background-color: white;
  border-radius: 1rem;
}

.cobalt-card-head {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-size: 1.5rem;
  padding: 3px;
}
.cobalt-img {
  width: 4rem;
  height: 4rem;
}
.cobalt-prices {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  font-size: 1rem;
  padding: 1rem;
}
.side-bar {
  background-color: white;
  width: 280px;
  height: 100vh;
  top: 0;
  box-shadow: 2px 2px 20px #3f51b5;
}

.side-bar li a {
  color: #060b26;
}
.side-bar li a:hover {
  background: #3f51b5;
  color: white;
}
@media (min-width: 768px) {
  .dashboard-card {
    width: 18rem;
  }
}
@media (max-width: 992px) {
  .side-bar {
    display: none;
  }
}
@media (min-width: 2009px) {
  .side-bar {
    left: 0;
    position: fixed;
    margin-top: 80px;
  }
}

.navbar {
    background-color: #3f51b5;
    width: 100%;
    height: 80px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
  }
  
  .logo {
    color: white;
    margin-left: 2rem;
  }
  .menu-bars {
    margin-right: 2rem;
    font-size: 2rem;
    background: none;
    background-color: #3f51b5;
  }
  
  .nav-menu {
    background-color: #3f51b5;
    width: 250px;
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
  }
  
  .nav-menu.active {
    left: 0;
    transition: 350ms;
  }
  
  .nav-text {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
  }
  
  .nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding: 0 16px;
    border-radius: 4px;
  }
  
  .nav-text a:hover {
    background-color: white;
    color: #060b26;
  }
  
  .nav-menu-items {
    width: 100%;
  }
  
  .navbar-toggle {
    background-color: #3f51b5;
    width: 100%;
    height: 80px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
  }
  
  span {
    margin-left: 16px;
  }
  
  .header-btns button {
    margin-right: 2rem;
    padding: 0.7rem;
    background: #3f51b5;
    color: white;
    border: 1.5px solid white;
    border-radius: 5px;
    cursor: pointer;
    font-weight: bold;
  }
  .header-btns button:hover {
    background: white;
    color: #3f51b5;
  }
  .side-btns {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  
  .side-btns button {
    margin: 1rem;
    background: #3f51b5;
    color: white;
    border: 1.5px solid white;
    border-radius: 5px;
    cursor: pointer;
    font-weight: bold;
    padding: 1rem;
  }
  .side-btns button:hover {
    background: white;
    color: #060b26;
  }
  
  @media (min-width: 992px) {
    .menu-bars {
      display: none;
    }
    .nav-menu {
      display: none;
    }
  }
  @media (max-width: 992px) {
    .header-btns {
      display: none;
    }
  }
  @media (max-width: 375px) {
    .navbar {
      width: 100vw;
    }
  }
  
